

/* Header styles */
.t-header-1,
.t-header-2,
.t-header-3 {
    line-height: 1.2;
    font-family: 'Open Sans', sans-serif;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-size: 1rem;
    box-sizing: border-box;
    border: 5px solid #ffffff;
    color: #ffffff;
    padding: 10px;
    font-weight: bold;
    position: relative;
}

.t-header-1 {
    background-color: #834496;
}

.t-header-2 {
    background-color: #f4a424;
}

.t-header-3 {
    background-color: #e65c7b;
}

/* Cell styles */
.cell {
    font-size: 1rem;
    font-family: 'Open Sans', sans-serif;
    color: #111111;
    text-align: center;
    line-height: 1.6;
    box-sizing: border-box;
    border: 3px solid #ffffff;
    padding: 10px;
    font-weight: inherit;
    position: relative;
}

/* Enroll button styling */
a.enroll {
    color: black;
    /* Black text color */
    text-decoration: none;
    /* Removes the underline */
    font-size: 18px;
    /* Font size */
    font-family: Arial, sans-serif;
    /* Font style */
    padding: 8px 16px;
    /* Padding for clickable area */
    transition: background-color 0.3s ease, color 0.3s ease;
    /* Smooth transition for hover effects */
}

a.enroll:hover {
    background-color: #555;
    /* Slightly lighter background on hover */
    color: #f1f1f1;
    /* Lighter text color on hover */
    border-radius: 5px;
    /* Rounded corners for the hover effect */
}

a.active {
    background-color: #4CAF50;
    /* Green background for active page */
    color: white;
    border-radius: 5px;
}

.compare-courses-wrapper {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 20px;
    margin-right: 20px;
    max-width: 100%;
}
.mark {
    height: 37px;
    width: 37px;
}
@media (max-width:480px) {

    .t-header-1,
    .t-header-2,
    .t-header-3 {
        line-height: 1.2;
        font-family: 'Open Sans', sans-serif;
        text-transform: uppercase;
        letter-spacing: 1px;
        font-size: 0.6rem;
        box-sizing: border-box;
        border: 5px solid #ffffff;
        color: #ffffff;
        padding: 10px;
        font-weight: bold;
        position: relative;
    }
    .compare-courses-wrapper {
        flex: auto;
        display:flex;
        justify-content: center;
        align-items: center;
        margin-left: 2px;
        margin-right: 2px;
        max-width: 80px;
    }
    .mark {
        height: 27px;
        width: 27px;
    }
}